.wrapper{
    display: flex;
}
.add_card{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 474px;
    height: 774px;
    margin: 40px;
    border-radius: 40px;
    background-image: url('../IMG/background/46.jpg');
    background-position: -10px -10px;
    box-shadow: 0px 0px 28px 0px rgba(0, 0, 0, 0.596);
    p{
        margin-top: 30px;
        font-size: 30px;
    }
    button{
        width: 92px;
        height: 92px;
        margin-top: 20px;
        border: none;
        border-radius: 100%;
        background-image: url('../IMG/background/Plus\ Icon.jpg');
        background-size:105%;
        background-position: -2px -1px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        &:active{
            border: none;
            outline: none;
            border-radius: 100%;
            box-shadow: 0px 0px 28px 0px rgba(0, 0, 0, 0.596);
        }
        &:focus{
            border: none;
            outline: none;
            border-radius: 100%;
        }
    }
    .location{
        display: flex;
        align-items:center;
        margin-top: 30px;
        .location_button{
            margin: 0;
        }
        p{
            font-size: 20px;
            margin-left: 20px;
        }
    }
}
.add_card_dark{
    background-image: url('../IMG/background/47.jpg');
    background-position: 0px -1px;
    box-shadow: 0px 0px 28px 0px rgba(255, 255, 255, 0.253);
    button{
        background-image: url('../IMG/background/PlusIconDarck.jpg');
        background-position:-5px -4px;
        background-size:112%;
        &:active{
            box-shadow: 0px 0px 28px 0px rgba(255, 255, 255, 0.397);
        }
    }
}
.form{
    display: flex;
    flex-direction: column;
    align-items:center;
    .valdation{
        font-size: 17px;
        margin:5px 0 0 0;
        color: rgb(255, 0, 0);
    }
}
@media screen and (max-width: 900px),(max-height: 750px)  {
    .add_card{
        min-width: 340px;
        height: 572px;
        padding: 10px;
        margin: 0px;
        margin-top: 20px;
        background-size:101%;
        button{
            margin-top: 20px;

        }
        .location{
            margin: 0;
        }
    }
}

